<template>
  <div class="page">
    <div id="me_head_modify">
      <!--圆角图标-->
      <div id="u_center_himg_modify">
      <img :src="titleBannerInfo.headImg"><span></span></div> 
      <!--昵称&微信名-->
      <div id="u_center_name_box_modify">
        <span id="u_wx_name">{{titleBannerInfo.subj}}</span>
        <span id="u_wx_no">{{titleBannerInfo.msg}}</span>
      </div>
    </div>
    <!--菜单-->
    <div class="cell me_menu">
      <img class="menu_icon" src="../../static/images/user-icon@2x.png">
      <div class="menu_name_modify">
        <input type="text" placeholder="姓名" v-model="userInfo.userName" >
      </div>
    </div>
    <div class="cell me_menu">
      <img class="menu_icon" src="../../static/images/phone-icon@2x.png">
      <div class="menu_name_modify">
        <input type="number" placeholder="电话" v-model="userInfo.userTelphone" oninput="if(value.length>11)value=value.slice(0,11)">
      </div>
    </div>
    <div class="cell me_menu">
      <img class="menu_icon" src="../../static/images/chepai-icon@2x.png">
      <div class="menu_name_modify">
        <div class="menu_msx">
          <span>{{car_no_char}}</span>
          <img src="../../static/images/jiaobiao@2x.png" @click="popSelect">
        </div>
        <input id="car_no" type="text" v-model="car_no"
        oninput="if(value.length>6)value=value.slice(0,6);value=value.toUpperCase()">
      </div>
    </div>
    <div class="commit_box">
      <el-button style="width:45%" @click="cencel">返 回</el-button>
      <el-button type="primary" style="width:45%" @click="submit">确 认</el-button>
    </div>
    <!-- 底部区号选择框 -->
    <div id="select_bar_bg" v-if="popUp" @click="popUp=false"></div><!-- 半透明遮罩层 -->
    <div id="select_bar" v-if="popUp">
      <div v-for="row in car_no_list">
        <span v-for="cell in row" @click="popItemSelect(cell)">{{cell}}</span>
      </div>
    </div>

  </div>
</template>

<script>
import {carReq} from '../port/ports'
export default {
  name: 'me',
  data () {
    return {
      qrCode:'',
      openId:'',
      userInfo:{},
      titleBannerInfo:{},
      popUp:false,
      popBgAnimRun:false,//背景动画执行中
      popBarAnimRun:false,//选择框动画执行中
      car_no_char:'渝',
      car_no:'',
      car_no_list:[
        ["渝","沪","津","京","辽","吉"],
        ["黑","蒙","冀","晋","陕","宁"],
        ["鲁","皖","苏","浙","豫","鄂"],
        ["湘","赣","闽","滇","琼","川"],
        ["黔","粤","桂","甘","新","藏"],
        ["青","港","澳","台"],
      ],
    }
  },
  created(){
    this.openId = localStorage.getItem("openId");
    this.qrCode = this.$route.query.qrCode;
    this.getTitleBannerInfo();
    this.getUserInfo();
    this.checkQRCode();
  },
  methods:{
    getTitleBannerInfo(){
      this.titleBannerInfo.headImg = '/static/images/logo@2x.png';
      this.titleBannerInfo.subj = '绑定车贴';
      this.titleBannerInfo.msg = 'Bind car code';
    },
    cencel(){
      window.history.go(-1);
    },
    getUserInfo(){//获取用户信息
      let req={
        openId:this.openId,
      };
      let self = this;
      this.post(carReq.userInfo,req,function(res){
        // console.log(res);
        if(res.data.code != 200){
          self.$message(res.data.msg);
          return;
        }
        self.userInfo = res.data.data;
        let carCode = self.userInfo.userCarCode + '';
        let carArea = '';
        let carNumb = '';
        if(carCode != 'undefined' && carCode){
          let carArea = carCode.substring(0,1);
          let carNumb = carCode.substring(1,carCode.length);
          self.car_no_char = carArea;
          self.car_no = carNumb;
        }
      });
    },
    checkQRCode(){
      let req={
        openId:this.openId,
        qrCode:this.qrCode,
      }
      let self = this;
      this.post(carReq.checkQRCode,req,function(res){
        console.log(res);
        if(res.code != 200){
          self.$message(res.msg);
        }
      });
    },
    popSelect(){
      this.popUp = !this.popUp;
      // let self = this;
      // console.log(this.popUp);
      // if(this.popUp){
      //   $("#select_bar_bg").fadeOut("fast",function(){
      //     self.popBgAnimRun = false;
      //     self.checkAnimEnd();
      //   });
      //   self.popBgAnimRun = true;
      // }else{
      //   $("#select_bar_bg").fadeIn("fast",function(){
      //     self.popBgAnimRun = false;
      //     self.checkAnimEnd();
      //   });
      //   self.popBgAnimRun = true;
      // }
    // },
    // checkAnimEnd(){
    //   console.log(this.popUp);
    //   if(this.popBgAnimRun||this.popBarAnimRun){
    //     return;
    //   }
    //   this.popUp = !this.popUp;
    },
    popItemSelect(cell){
      this.car_no_char = cell;
      this.car_no = '';
      this.popUp = false;
    },
    menuClick(item){//菜单点击跳转页面
      this.$router.push({path:item.url});
    },
    submit(){
      if(!this.userInfo.userName){
        this.$message("用户名不能为空！");
        return;
      }
      if(!this.phoneReg.test(this.userInfo.userTelphone)){
        this.$message("请输入正确的电话号码！");
        return;
      }
      let req = {
        openId:this.openId,  
        userName:this.userInfo.userName,
        carCode:this.car_no_char + this.car_no,
        telphone:this.userInfo.userTelphone,
        qrCode:this.qrCode,
      };
      // console.log(req);
      let self = this;
      this.post(carReq.userBind,req,function(res){
        console.log(res);
        if(res.data.code != 200){
          self.$message(res.data.msg);
          return;
        }
        self.$message("绑定成功！");
      });
    },
  },
}
</script>

<style scoped>
.page{
  width: 100%;
}
.me_menu{
  height: 20px;
  padding: 20px;
}
.menu_icon{
  float: left;
  width: 25px;
  height: 25px;
  margin-right: 10px;
}
.menu_name_modify{
  float: left;
  color: #666;
  font-size: 19px;
  height: 30px;
  width: 60%;
  text-align: left;
  line-height: 10px;
  width: 80%;
}
.menu_name_modify input{
  font-size: 20px;
  width: 80%;
  outline:medium;
  border: none;
  color: #666;
}
#car_no{
  float: left;
  width: 50%;
  font-size: 20px;
  background: #fff;
}
.menu_msx{
  float: left;
  width: 20%;
  margin-top: 7px;
}
#me_head_modify{
  height: 200px;
  padding: 20px;
  background: #263238;
  color: white;
  text-align: center;
}
#u_center_himg_modify{
  text-align: center;
  width: 100%;
  height: 130px;
}
#u_center_himg_modify img{
  width: 130px;
  height: 130px;
  margin-left: 20px; 
}
#u_center_name_box_modify{
  padding-top: 20px;
  width: 100%;
  color: white;
}
#u_center_name_box_modify span{
  display: block;
  width: 100%;
  text-align: center;
  margin: 5px;
}
#u_wx_name{
  font-weight: 8;
  font-size: 18px;
}
#u_wx_no{
  font-size: 12px;
}
#select_bar_bg{
  opacity: 0.3;
  background: #FFF;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
#select_bar{
  background: #F2F2F2;
  position: fixed;
  bottom: 0;
  left: 0;
  padding: 8%;
  width: 100%;
  height: 30%;
  -webkit-border-radius:1%;
  -moz-border-radius:1%;
  -ms-border-radius:1%;
  -o-border-radius:1%;
}
#select_bar > div{
  text-align: left;
  width: 100%;
  height: 30px;
}
#select_bar span{
  height: 20px;
  padding: 1% 4%;
  margin: 3px;
  background: white;
}
</style>


