<template>
  <div class="page">
    <div class="record cell" v-for="record in records" ref="record">
      <div>
      	<img class="record_icon" :src="getRemindIcon(record.recordType)">
	    <span class="record_name">{{getRemindType(record.recordType)}}</span>
	    <span class="record_time">{{formatTime(record.recordTime)}}</span>
      </div>
      <div class="record_msg">{{record.recordCount}}</div>
    </div>
  </div>
</template>

<script>
import {carReq} from '../port/ports'
const handletime = require('../mixins/handletime.js').ports   //引入时间处理函数对象
export default {
  name: 'me',
  data () {
    return {
      openId:'',
      records:[],
      limitStart:1,
      pageSize:20,
      listReqIng:false,
    }
  },
  created(){
    this.openId = localStorage.getItem("openId");
    this.reqRecord();
    // var se = document.documentElement.clientHeight //浏览器可见区域高度。
    // console.log(se);

    let self = this;
    window.onscroll= function(){
      //变量t是滚动条滚动时，距离顶部的距离
      var t = document.documentElement.scrollTop;
      // console.log(t);
      if(t>=1000*self.limitStart){
        if(self.listReqIng){
          return;
        }
        self.limitStart++;
        self.reqRecord();
      }
    };

  },
  methods:{
    reqRecord(){
      let self = this;
      let req = {
        openId:this.openId,
        limitStart:this.limitStart,
        pageSize:this.pageSize,
      }
      this.listReqIng = true;
      // console.log(req);
      this.post(carReq.remindList,req,function(res){
        // console.log(res);
        self.listReqIng = false;
        if(res.data.code != 200){
          self.$message(res.data.msg);
          return;
        }
        res.data.data.forEach(function(item){
          self.records.push(item);
        });
      });
    },
    getRemindIcon(recordType){
      if(recordType == 1){
        return '/static/images/call@2x.png';
      }else{
        return '/static/images/xinxi@2x.png';
      }
    },
    getRemindType(recordType){
      if(recordType == 1){
        return '语音留言';
      }else{
        return '信息留言';
      }
    },
    formatTime(timestamp){
      return handletime.timeFormat(timestamp);
    },
  },
}
</script>

<style scoped>
.record{
  height: 80px;
  padding: 10px;
}  
.record_icon{
  width: 20px;
  height: 20px;
  margin-right: 10px;
  float: left;
}
.record_name{
  font-size: 16px;
  float: left;	
}
.record_time{
  font-size: 14px;
  float: right;
}
.record_msg{
  display: block;
  font-size: 14px;
  min-width: 250px;
  float: left;
  text-align: left;
  margin-top: 5px;
  padding-left: 30px;	
}
</style>


