<template>
  <div class="page">
    <div id="me_head" class="cell">
      <!--圆角头像-->
      <div id="u_center_himg" class="round_img_box"><img :src="userInfo.userWxImg"><span></span></div> 
      <!--昵称&微信名-->
      <div id="u_center_name_box">
        <span id="u_wx_name">{{userInfo.userName}}</span>
        <span id="u_wx_no">{{userInfo.userWxName}}</span>
      </div>
      <!--二维码&箭头-->
      <img id="qrcode" src="../../static/images/二维码-小@2x.png" @click="meQRClick">
      <img id="arrow" src="../../static/images/arrow-right@2x.png" @click="meInfoClick">
      <div style="font-size:10px;">绑定车贴</div>
    </div>
    <!--菜单-->
    <div class="cell me_menu" v-for="item in menu" @click="menuClick(item)">
      <img class="menu_icon" :src="item.icon">
      <span class="menu_name">{{item.name}}</span>
      <img class="menu_arrow" src="../../static/images/arrow-right@2x.png">
    </div>
  </div>
</template>

<script>
import {carReq} from '../port/ports'
export default {
  name: 'me',
  data () {
    return {
      openId:'',
      userInfo:{
        userName:''
      },
      menu:[
        {
          icon:'/static/images/个人信息@2x.png',
          name:'个人信息',
          url:'./userInfo',
        },{
          icon:'/static/images/挪车记录@2x.png',
          name:'挪车记录',
          url:'./moveRecord',
        },{
          icon:'/static/images/二维码@2x.png',
          name:'二维码',
          url:'./myQrCode',
        },
      ],
      wxSignJson:'',
      canScan:false,
    }
  },
  created(){
    this.openId = localStorage.getItem("openId");
    this.getUserInfo();
    this.getWXSignStr();
  },
  methods:{
    getUserInfo(){//获取用户信息
      let req={
        openId:this.openId,
      };
      let self = this;
      console.log(req);
      this.post(carReq.userInfo,req,function(res){
        // console.log(res);
        if(res.data.code != 200){
          self.$message(res.data.msg);
          return;
        }
        self.userInfo = res.data.data;
      });
    },

    getWXSignStr(){//获取微信签名信息
      let self = this;
      this.post(carReq.getWXSignStr,{},function(res){
        self.wxSignJson = res.data.data;
        self.readScan();
      })
    },
    readScan(){
      let self = this;
      wx.config({
        debug : false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
        appId : self.wxSignJson.appId, // 必填，公众号的唯一标识
        timestamp : self.wxSignJson.timestamp, // 必填，生成签名的时间戳
        nonceStr : self.wxSignJson.nonceStr, // 必填，生成签名的随机串
        signature : self.wxSignJson.signature,// 必填，签名，见附录1
        jsApiList : [ 'scanQRCode' ]
        // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
      });
      wx.ready(function(){
        self.canScan = true;
        // self.scanQRCode();
      });
      wx.error(function(res){
          alert("打开摄像头失败");
      });
    },
    scanQRCode(){
      wx.scanQRCode({
         desc: 'scanQRCode desc',
         needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
         scanType: ["qrCode","barCode"], // 可以指定扫二维码还是一维码，默认二者都有
         success: function (res) {
             // 回调
             var redirectUrl = "http://chet.qmcs-china.com/#/carBind";//扫描结果重定向到绑定车贴页面
             var codeStr = res.resultStr;
             var arr = codeStr.split("code=")[1];
             if (typeof(arr) == "undefined"){
               alert("无效二维码");
               return;
             }
             var qrCode = arr.substring(0,15);
             if(qrCode.indexOf("NCT") == -1){
               alert("无效二维码");
               return;
             }
             window.location.href = redirectUrl + "?qrCode=" + qrCode;
         },
         error: function(res){
             if(res.errMsg.indexOf('function_not_exist') > 0){
                 alert('版本过低请升级');
             }
         }
     });
    },
    meInfoClick(){//点击顶部箭头进入个人中心
      this.menuClick(this.menu[0]);
    },
    meQRClick(){//点击顶部二维码扫描车贴二维码开始绑定
      // this.get("http://chet.qmcs-china.com/bindCode",{},function(res){

      // });
      // location.href = "http://chet.qmcs-china.com/bindCode";
      this.scanQRCode();
    },
    menuClick(item){//菜单点击跳转页面
      // console.log(item);
      this.$router.push({path:item.url});
    },
  },
}
</script>

<style scoped>
  .me_menu{
    height: 20px;
    padding: 25px;
  }
  .menu_icon{
    float: left;
    width: 20px;
    height: 20px;
    margin-right: 10px;
  }
  .menu_name{
    float: left;
    color: #666;
    font-size: 18px;
    line-height: 23px;
  }
  .menu_arrow{
    float: right;
    padding: 5px;
    width: 11px;
    height: 19px;
  }
  #me_head{
    height: 100px;
    margin-bottom: 10px;
    padding: 20px;
  }
  #u_center_himg{
    float: left;
    width: 100px;
    height: 100px;
  }
  #u_center_name_box{
    float: left;
    width: 32%;
    height: 80px;
    margin-left: 20px;
    padding-top: 20px;
    vertical-align: middle;
  }
  #u_center_name_box span{
    display: block;
    width: 100%;
    text-align: left;
    margin: 5px;
  }
  #u_wx_name{
    color: #333;
    font-weight: 8;
    font-size: 18px;
  }
  #u_wx_no{
    color: #333;
    font-size: 12px;
  }
  #qrcode{
    margin-top: 30px;
    margin-left: 20px;
    padding: 6px;
    width: 19px;
    height: 19px;
  }
  #arrow{
    float: right;
    margin-top: 30px;
    padding: 6px;
    width: 11px;
    height: 19px;
    /*border: 1px solid blue;*/
  }
</style>


