<template>
  <div class="page">
    <div id="me_head_modify" class="cell">
      <!--圆角图标-->
      <div id="pay_himg" class="round_img_box"><img :src="payInfo.icon"><span></span></div> 
      <!--昵称&微信名-->
      <div id="pay_hbox">
        <span id="u_wx_name">{{payInfo.sub}}</span>
        <span id="u_wx_no">{{payInfo.content}}</span>
      </div>
    </div>
    <!--菜单-->
    <div class="cell pay_menu">
      <div class="menu_name_pay">物品金额</div>
      <div class="menu_value_pay">{{payObj.orderPrice}}元</div>
    </div>
    <div class="cell pay_menu">
      <div class="menu_name_pay">快递费</div>
      <div class="menu_value_pay">{{payObj.transPrice}}元</div>
    </div>
    <div class="cell pay_menu">
      <div class="menu_name_pay m_total">总金额</div>
      <div class="menu_value_pay m_total">￥{{payObj.totalPrice}}</div>
    </div>
    <div class="commit_btn" @click="submit">确认</div>

  </div>
</template>

<script>
import {carReq} from '../port/ports'
import {wx} from '../port/ports'
export default {
  name: 'me',
  data () {
    return {
      payInfo:{
        icon:'',
        sub:'',
        content:'',
      },
      payObj:{
        orderPrice:'',
        transPrice:'',
        totalPrice:'',
      },
      orderId:'',
    }
  },
  created(){
    this.getPayInfo();

    let number = this.$route.query.number;
    let postPrice = this.$route.query.transPrice;

    this.payObj.orderPrice=parseFloat(number).toFixed(2);
    this.payObj.transPrice=parseFloat(postPrice).toFixed(2);
    let totalPrice = parseFloat(number) + parseFloat(postPrice);
    this.payObj.totalPrice=totalPrice.toFixed(2);

    this.orderId=this.$route.query.orderId;
  },
  methods:{
    getPayInfo(){//获取支付信息
      this.payInfo.icon = '/static/images/logo@2x.png';
      this.payInfo.sub = '确认支付信息';
      this.payInfo.content = '温馨提示，车贴1元一个，5个起售';
    },
    submit(){
      // console.log(this.orderId);
      this.post(carReq.pay,{orderId:this.orderId},function(res){
        // console.log(res);
        if(res.data.code != 200){
          self.$message(res.data.msg);
          return;
        }
        //TODO 发起微信支付
      });
    },
  },
}
</script>

<style scoped>
  .pay_menu{
    height: 20px;
    padding: 25px;
  }

  .menu_icon{
    float: left;
    width: 25px;
    height: 25px;
    margin-top: 10px;
    margin-right: 10px;
  }
  .menu_name_pay{
    float: left;
    color: #666;
    font-size: 15px;
    height: 20px;
    line-height: 20px;
  }
  .menu_value_pay{
    float: right;
    color: #666;
    font-size: 15px;
    height: 20px;
    line-height: 20px;
  }
  .m_total{
    color: #4CA8B4;
  }
  #me_head_modify{
    height: 200px;
    padding: 20px;
    background: #263238;
    color: white;
    text-align: center;
    /*border: 1px solid red;*/
  }
  #pay_himg{
    text-align: center;
    width: 100%;
    height: 130px;
    /*border: 1px solid blue;*/
  }
  #pay_himg img{
    width: 130px;
    height: 130px;
    margin-left: 100px;
    /*border: 1px solid green;*/
  }
  #pay_hbox{
    padding-top: 20px;
    width: 100%;
    color: white;
    /*border: 1px solid #979468;*/
  }
  #pay_hbox span{
    display: block;
    width: 100%;
    text-align: center;
    margin: 5px;
  }
  #u_wx_name{
    font-weight: 8;
    font-size: 18px;
  }
  #u_wx_no{
    font-size: 12px;
  }
  #select_bar_bg{
    opacity: 0.3;
    background: #FFF;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  #select_bar{
    background: #F2F2F2;
    position: fixed;
    bottom: 0;
    left: 0;
    padding: 20px;
    width: 100%;
    height: 30%;
    -webkit-border-radius:1%;
    -moz-border-radius:1%;
    -ms-border-radius:1%;
    -o-border-radius:1%;
  }
  #select_bar > div{
    text-align: left;
    width: 100%;
    height: 30px;
  }
  #select_bar span{
    width: 35px;
    height: 20px;
    padding: 5px 17px;
    margin: 5px 3px;
    background: white;
  }
</style>


