import qs from 'qs'
export default{
  data () {
    return {
      phoneReg:/^1[34578]\d{9}$/,
      // openId:"ox4tTwZE9ulbDoywQzSRPrIKLbsA",
    }
  },
	methods: {
    post:function(URL,body,callback,errorback){//goback为调用此方法的方法
      // console.log(qs.stringify(body));
      this.$axios({
      	method:'post',
	      url:URL, // 请求路径
	      timeout:'3000',
	      headers: {
	      	'Content-Type':'application/x-www-form-urlencoded',
          // 'Content-Type':'application/json',
        },
	      data:qs.stringify(body),//请求参数
        // datatype: 'form',
	      // withCredentials:true,   //允许跨域携带cookit
	    })
      .then(callback)//成功回调
      .catch(errorback); //失败回调
    },
    postp:function(URL,body,callback,errorback){//goback为调用此方法的方法
      // console.log(qs.stringify(body));
      this.$axios({
        method:'post',
        url:URL, // 请求路径
        timeout:'3000',
        headers: {
          'Content-Type':'application/x-www-form-urlencoded',
          // 'Content-Type':'application/json',
        },
        data:qs.stringify(body),//请求参数
        dataType: 'json',
        crossDomain: true,
        // withCredentials:true,   //允许跨域携带cookit
      })
      .then(callback) //成功回调
      .catch(errorback); //失败回调
    },
    get:function(URL,parameters,callback,errorback){//goback为调用此方法的方法
      // console.log(qs.stringify(parameters));
      
      if(parameters != null && parameters != 'undefined'){
        URL += qs.stringify(parameters);
      }
      // console.log(URL);
      this.$axios({
        method:'get',
        url:URL, // 请求路径
        timeout:'3000',
        headers: {
          'Content-Type':'application/x-www-form-urlencoded',
          // 'Content-Type':'application/json',
        }
      })
      .then(callback) //成功回调
      .catch(errorback); //失败回调
    },


    currentLocation(complete,error){
      var map, geolocation;
      //加载地图，调用浏览器定位服务
      map = new AMap.Map('container', {
          resizeEnable: true
      });
      map.plugin('AMap.Geolocation', function() {
        geolocation = new AMap.Geolocation({
            enableHighAccuracy: true,//是否使用高精度定位，默认:true
            timeout: 3000,          //超过10秒后停止定位，默认：无穷大
            buttonOffset: new AMap.Pixel(10, 20),//定位按钮与设置的停靠位置的偏移量，默认：Pixel(10, 20)
            zoomToAccuracy: true,      //定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：false
            buttonPosition:'RB'
        });
        map.addControl(geolocation);
        geolocation.getCurrentPosition();
        AMap.event.addListener(geolocation, 'complete', complete);//返回定位信息
        AMap.event.addListener(geolocation, 'error', error);      //返回定位出错信息
      });
    },
    
  }
}


