<template>
  <div class="page align_center">
    <img id="res_logo" :src="logo">
    <div id="res_info">{{info}}</div>
  </div>
</template>

<script>
export default {
  name: 'me',
  data () {
    return {
      logo:'',
      info:'',
    }
  },
  created(){
    let res = this.$route.query.res;
    // console.log(res);
    if(res == "success"){
      this.logo = "/static/images/logo@2x.png";
      this.info = "噢，成功了";
    }else{
      this.logo = "/static/images/logo@2x.png";
      this.info = "噢，失败了";
    }
  },
  methods:{
    
  },
}
</script>

<style scoped>
#res_logo{
  width: 100px;
  height: 100px;
  margin: 20px 10px;
}
#res_info{
  font-size: 18px;
}
</style>


