<template>
  <div id="scan_page" class="page align_center">
    <img id="head_banner" :src="banner">
    <div id="remind_tips">{{info}}</div>
    <div class="remind_box" v-for="item in icons" @click="clickIcon(item)">
      <img :src="item.icon">
    </div>
  </div>
</template>

<script>
import {carReq} from '../port/ports'
export default {
  name: 'me',
  data () {
    return {
      openId:'',
      qrCode:'',
      banner:'../../static/images/banner@2x.png',
      info:'选择电话或者信息的方式提醒对方',
      icons:[
        {
          type:1,
          icon:'/static/images/电话按钮@2x.png',
        },
        {
          type:0,
          icon:'/static/images/信息按钮@2x.png',
        }
      ],
    }
  },
  created(){
    this.openId = localStorage.getItem("openId");
    // this.qrCode = 'QMCS0001'; //暂时写死
    this.qrCode = this.$route.query.qrCode;
  },
  methods:{
    clickIcon(item){
      let req={
        openId:this.openId,
        qrCode:this.qrCode,
        type:item.type,
      };
      let self = this;
      this.post(carReq.sendRemind,req,function(res){
        self.$message(res.data.msg);
      });
    }
  },
}
</script>

<style scoped>
#scan_page{
  background: white;
}
#head_banner{
  width: 100%;
}
#remind_tips{
  font-size: 14px;
  color: #4FA9B5;
  margin: 20px; 
}
.remind_box{
  background: #D9EDF9;
  text-align: center;
  padding: 30px;
  margin: 20px;
}
.remind_box img{
  width: 80px;
  height: 80px;
}
</style>


