//处理时间方法 把毫秒时间搓格式化为显示格式
exports.ports={
 timeFormat:function(timenum){
   if(timenum == 'undefined' || timenum == 'null'|| timenum == ''|| timenum == 0 || timenum == null || timenum == undefined){return "无"}
   var unixTimestamp = new Date(parseInt(timenum));
   var commonTime = unixTimestamp.toLocaleString('chinese',{hour12:false});
   return commonTime;
 },
 timeChange:function(date,type){ //1为起始时间；2为结束时间
  if(date == '' || date == undefined){
  	return null;
  }
  var year = date.getFullYear();
	var month = date.getMonth();
	var day = date.getDate();
	switch(type)
	{
	  case 1:return new Date(Date.UTC(year,month,day,-8,0,0)).getTime();break;
	  case 2:return new Date(Date.UTC(year,month,day,15,59,59)).getTime();break;
	}
 },
 backDate:function(date,type){
  if(date == 'undefined' || date == 'null'|| date == ''|| date == 0){return ""}
  switch(type)
  {
   case 'month':return date.getFullYear()+'-'+((date.getMonth()+1) < 10?'0'+(date.getMonth()+1):date.getMonth()+1);break;
   case 'year':return date.getFullYear();break;
   case 'day':return date.getFullYear()+'-'+((date.getMonth()+1) < 10?'0'+(date.getMonth()+1):date.getMonth()+1)+'-'+(date.getDate() < 10?'0'+ date.getDate():date.getDate());break;  
  }
 }
}

 




