import Vue from 'vue'

import carBind from '@/components/car_bind'
import me from '@/components/me'
import moveRecord from '@/components/move_record'
import userInfo from '@/components/user_info'
import pay from '@/components/pay'
import apply from '@/components/apply'
import addrSearch from '@/components/addr_search'
import handlerResult from '@/components/handle_result'
import scanResult from '@/components/scan_result'
import myQrCode from '@/components/my_qr_code'

import Router from 'vue-router'
Vue.use(Router)

import VueWechatTitle from 'vue-wechat-title';  
Vue.use(VueWechatTitle)

export default new Router({
  // mode:'history',
  routes: [
    {
      path: '/',
      name: 'apply',
      meta: {
               title: '申请车贴'
          },
      component: apply
    },
    {
      path: '/me',
      name: 'me',
      meta: {
               title: '我的信息'
          },
      component: me
    },
    {
      path: '/userInfo',
      name: 'userInfo',
      meta: {
               title: '修改个人信息'
          },
      component: userInfo
    },
    {
      path: '/carBind',//绑定车贴
      name: 'carBind',
      meta: {
               title: '绑定车贴'
          },
      component: carBind
    },
    {
      path: '/pay',
      name: 'pay',
      meta: {
               title: '确认支付'
          },
      component: pay
    },
    {
      path: '/apply',
      name: 'apply',
      meta: {
               title: '申请车贴'
          },
      component: apply
    },
    {
      path: '/addrSearch',
      name: 'addrSearch',
      meta: {
               title: '地址搜索'
          },
      component: addrSearch
    },
    {
      path: '/handlerResult',
      name: 'handlerResult',
      meta: {
               title: '结果'
          },
      component: handlerResult
    },
    {
      path: '/moveRecord',
      name: 'moveRecord',
      meta: {
               title: '挪车记录'
          },
      component: moveRecord
    },
    {
      path: '/scanResult',//通知挪车
      name: 'scanResult',
      meta: {
               title: '通知挪车'
          },
      component: scanResult
    },
    {
      path: '/myQrCode',
      name: 'myQrCode',
      meta: {
               title: '我的车贴'
          },
      component: myQrCode
    },
  ]
})



