<template>
  <div class="page">
    <div id="qr_layer"></div>
    <div id="qr_box">
      <div id="user_info">
        <!--圆角头像-->
        <div id="u_center_himg" class="round_img_box"><img :src="userInfo.userWxImg"><span></span></div>
        <div>
          <span id="u_name">{{userInfo.userName}}</span>
          <span>{{userInfo.userWxName}}</span>
        </div>
      </div>
      <div id="qr_img"></div>
    </div>
  </div>
</template>

<script>
import {carReq} from '../port/ports'
export default {
  name: 'me',
  data () {
    return {
      openId:'',
      userInfo:{},
      qrcode:{},
    }
  },
  created(){
    this.openId = localStorage.getItem("openId");
    this.getUserInfo();
  },
  methods:{
    getUserInfo(){//获取用户信息
      let req={
        openId:this.openId,
      };
      let self = this;
      this.post(carReq.userInfo,req,function(res){
        console.log(res);
        if(res.data.code != 200){
          self.$message(res.data.msg);
          return;
        }
        self.userInfo = res.data.data;
        self.createQrCodeImg();
      });
    },
    createQrCodeImg(){
      var qrcode = new QRCode(document.getElementById("qr_img"), {
          width : 200,//设置宽高  
          height : 200
      });
      let codeUrl = 'http://weixin.qq.com/r/ETpXT4nEktTtrc0s928D&actionType=noneAction&codeType=qr&code=' + this.userInfo.userQrCode + '&actionType=noneAction&codeType=qr';
      qrcode.makeCode(codeUrl);
    },
  },
}
</script>

<style scoped>
#qr_layer{
  position: fixed;
  width: 100%;
  height: 100%;
  background: black;
  z-index: 1;
  opacity: 0.7;
}
#qr_box{
  position: fixed;
  top: 30px;
  left: 30px;
  right: 30px;
  padding: 30px;
  background: white;
  z-index: 2;
}
#qr_img{
  display: block;
  margin: auto;
  width: 200px;
  height: 200px;
}
#user_info{
  margin-bottom: 80px;
}
#u_center_himg,#user_info img{
  width: 50px;
  height: 50px;
  float: left;
  margin-right: 10px;
}
#user_info div{
  float: left;
  text-align: left;
}
#user_info div span{
  display: block;
  margin: 5px;
}
#u_name{
  font-weight: bold;
  font-size: 18px;
}
</style>


