<template>
  <div id="app">
    <router-view v-wechat-title="$route.meta.title"/>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
     
    }
  },
  created(){
    this.GetOpenId();
  },
  mounted(){

  },
  methods: {
    GetOpenId(){
      let openId  = this.$route.query.openId;
      // openId = "ox4tTwZE9ulbDoywQzSRPrIKLbsA";
      localStorage.setItem("openId",openId);
    },
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin: -2.1%;
}
.align_center{
  text-align: center;
}
.page{
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  bottom: 0;
  left: 0;
  background: #EEE;
}
.cell{
  background: #FFF;
  opacity: 1;
  border-bottom: 1px solid #D0D0D0;
}
.round_img_box{
  position:relative; 
  top:0; 
  left:0;
}
.round_img_box img{
  position:absolute; 
  top:0; 
  left:0;
  width:100%;
  height:100%;
  -webkit-border-radius:50%;
  -moz-border-radius:50%;
  -ms-border-radius:50%;
  -o-border-radius:50%;
}
.commit_btn{
  background: #4FA9B5;
  margin: auto;
  margin: 5%;
  width: 90%;
  height: 50px;
  line-height: 50px;
  color: white;
  -webkit-border-radius:5px;
  -moz-border-radius:5px;
  -ms-border-radius:5px;
  -o-border-radius:5px;
}
.commit_box{
  background: #fff;
  padding: 20px;
}
.float_but{
  position: fixed;
  right: 5%;
  bottom: 20%;
  background: #aaa;
  width: 40px;
  height: 40px;
  z-index: 9;
  border-radius: 20px;
}
.float_but > img{
  width: 35%;
  height: 60%;
  margin: 8px;
}
</style>



