<template>
  <div class="page">
    <div id="me_head_modify" class="cell">
      <!--圆角图标-->
      <div id="apply_himg" class="round_img_box"><img :src="applyInfo.headImg"><span></span></div> 
      <!--提示信息-->
      <div id="apply_name_box">
        <span id="u_wx_name">{{applyInfo.sub}}</span>
        <span id="u_wx_no">{{applyInfo.msg}}</span>
      </div>
    </div>
    <!--菜单-->
    <div class="cell me_menu">
      <img class="menu_icon" src="../../static/images/姓名@2x.png">
      <div class="menu_name_modify">
        <input type="text" placeholder="姓名" v-model="receiver.name">
      </div>
    </div>
    <div class="cell me_menu">
      <img class="menu_icon" src="../../static/images/电话@2x.png">
      <div class="menu_name_modify">
        <input type="number" placeholder="电话" v-model="receiver.cell" oninput="if(value.length>11)value=value.slice(0,11)">
      </div>
    </div>
    <div class="cell me_menu">
      <img class="menu_icon" id="addr_icon" src="../../static/images/矢量智能对象@2x.png">
      <div class="menu_name_modify">
        <textarea id="addr_receive" type="text" placeholder="省/市/区/县" v-model="receiver.addrWhole" @click="addrSearchPop=true"></textarea> 
      </div>
      <span @click="getLoc">定位当前</span>
    </div>

    <div class="cell me_menu">
      <div class="menu_name_modify">
        <input id="detail_addr" type="text" placeholder="填写详细地址" v-model="receiver.addrDetail">
      </div>
    </div>

    <div class="me_menu" id="number_div">
      <div class="menu_name_modify _width">
        <a id="number_lable">选择数量</a>
        <div class="number_right">
        <img id="number_dec" src="../../static/images/减@2x.png" @click="numDec">
        <span id="number_value">{{orderReq.number}}</span>
        <img id="number_inc" src="../../static/images/加@2x.png" @click="numInc">
        </div>
      </div>
    </div>
    <div class="commit_btn" @click="submit">确认</div>
    <v-addr_search id="addr_pop_search" v-if="addrSearchPop" @select="selectPop"></v-addr_search>
  </div>
</template>

<script>
import addrSearch from "./addr_search"
import {carReq} from '../port/ports'
import {wx} from '../port/ports'
import qs from 'qs'
export default {
  name: 'me',
  components:{
    "v-addr_search":addrSearch
  },
  data () {
    return {
      applyInfo:{
        sub:'',
        msg:'',
      },

      openId:'',
      orderReq:{
        username:'',
        telphone:'',
        qmcsNo:'',
        address:'',
        orderId:'',
        distance:'',
        number:5,
        transPrice:'',
      },

      receiver:{
        name:'',
        cell:'',
        addrWhole:'',
        lat:'',
        lng:'',
        adcode:'',
        addrDetail:'',
      },
      addrSearchPop:false,
    }
  },
  created(){
    this.openId = localStorage.getItem("openId");
    this.getApplyInfo();
  },
  methods:{
    getApplyInfo(){//获取用户信息
      this.applyInfo.headImg = '/static/images/logo@2x.png';
      this.applyInfo.sub = '请填写收货地址';
      this.applyInfo.msg = '温馨提示，车贴1元一个，5个起售';
    },
    numInc(){
      if(this.orderReq.number < 99){
        this.orderReq.number++;
      }
    },
    selectPop(addr){
      this.addrSearchPop = false;
      if(!addr){
        return;
      }
      // console.log(addr);
      this.receiver.lat = addr.lat;
      this.receiver.lng = addr.lng;
      this.receiver.adcode = addr.adcode;
      this.receiver.addrWhole = addr.addr;
    },
    numDec(){
      if(this.orderReq.number > 5){
        this.orderReq.number--;
      }
    },

    getLoc(){
      this.getRequirTime();
      let self = this;
      this.currentLocation(function(res){
        // console.log(res);
        self.receiver.addrWhole = res.formattedAddress;
        self.receiver.adcode = res.addressComponent.adcode;
        let loc = res.position;
        self.receiver.lat = loc.lat;
        self.receiver.lng = loc.lng;
      },function(err){
        console.log(err);
      });
    },

    createTransOrder(){
      let crebody = {
        senderName:"重庆标远科技发展有限公司",
        senderTel:"4006607997",
        sendAreaId:"500103010",
        sendAddr:"重庆市渝中区解放碑街道新华大厦43楼",
        sendLng:"106.577805",
        sendLat:"29.553872",
        receiverName:this.receiver.name,
        receiverTel:this.receiver.cell,
        receiverAreaId:this.receiver.adcode,
        receiverAddr:this.receiver.addrWhole,
        receiverLng:this.receiver.lng,
        receiverLat:this.receiver.lat,
        goodsSize:15,
        goodsWeight:2,
        goodsType:1,
        requiredTime:this.getRequirTime(),
        openId:this.openId,
        distance:this.orderReq.distance,
      }

      // console.log(crebody);
      let self = this;
      this.post(wx.createOrder,crebody,function(res){
        // console.log(res);
        if(res.data.code == 200){
          self.orderReq.qmcsNo = res.data.data.orderNo;
          self.createCarOrder();
        }
      });
    },

    createCarOrder(){
      let carOrder = {
        username:this.receiver.name,//收件人
        telphone:this.receiver.cell,//  联系电话
        address:this.receiver.addrWhole,//  地址
        qmcsNo:this.orderReq.qmcsNo,//  快件单好
        price:this.orderReq.number,//  价格
        postPrice:this.orderReq.transPrice, //运费价
        openId:this.openId, 
        number:this.orderReq.number, // 数量
      }
      // console.log(carOrder);
      let self = this;
      this.post(carReq.createOrder,carOrder,function(res){
        // console.log(res);
        if(res.data.code == 200){
          self.orderReq.orderId = res.data.data.orderId;
          self.toPay();
        }
      });
    },

    calcuPrice(){
      let crebody = {
        goodsWeight:2,
        openId:this.openId,
        distance:this.orderReq.distance,
      }

      // console.log(crebody);
      let self = this;
      this.post(wx.orderPrice,crebody,function(res){
        // console.log(res);
        if(res.data.code == 200){
          self.orderReq.transPrice = res.data.data.price;
          self.createTransOrder();
        }
      });
    },

    calcuDistance(){
      let consigneesLocs = "106.577805,29.553872";
      let sendLoc = this.receiver.lng + "," +  this.receiver.lat;
      var parameters = {'key':'c654249bf032c01f4924ee95ac160252','origins':consigneesLocs,'destination':sendLoc};
      let self = this;
      this.get("http://restapi.amap.com/v3/distance?",parameters,function(res){
        // console.log(res);
        if(res.status != 200){
          return;
        }
        if(res.data.status == 0){
          return;
        }
        let dis = res.data.results;
        let distance = dis[0].distance/1000;
        let distanceFixed = distance.toFixed(2);
        self.orderReq.distance = distanceFixed;
        self.calcuPrice();
      });
    },
  
    submit(){
      if(!this.receiver.name){
        this.$message("请输入收件人姓名");
        return;
      }
      if(!this.phoneReg.test(this.receiver.cell)){
        this.$message("请输入正确的电话号码！");
        return;
      }
      if(!this.receiver.addrWhole){
        this.$message("请输入收件地址");
        return;
      }
      this.calcuDistance();
    },

    toPay(){
      let param = "?num=" + this.number +"&price=" + this.price;

      this.orderReq.username = this.receiver.name;
      this.orderReq.telphone = this.receiver.cell;
      this.orderReq.address = this.receiver.addrWhole;

      let path = "pay?"+qs.stringify(this.orderReq);
      // console.log(path);
      this.$router.push({path:path});
    },

    getRequirTime(){
      let now = new Date();
      return now.getTime()+7200;
    },
  },
}
</script>

<style scoped>
  .me_menu{
    height: 40px;
    padding: 10px;
  }
  .me_menu span{
    padding-left: 5px;
    padding-right: 5px;
    line-height: 40px;
    color: #4FA9B5;
    float: right;
  }
  .menu_icon{
    float: left;
    width: 20px;
    height: 20px;
    margin-top: 10px;
    margin-right: 10px;
  }
  .menu_name_modify{
    float: left;
    color: #666;
    width: 60%;
    font-size: 15px;
    height: 40px;
    line-height: 40px;
  }
  .menu_name_modify input,.menu_name_modify textarea{
    width: 230px;
    font-size: 16px;
    outline:medium;
    border: none;
    vertical-align: middle;
    /*border: 1px solid blue;*/
  }
  

  #me_head_modify{
    height: 230px;
    padding: 20px;
    background: #263238;
    color: white;
    text-align: center;
    /*border: 1px solid red;*/
  }
  #apply_himg{
    text-align: center;
    width: 100%;
    height: 130px;
    /*border: 1px solid blue;*/
  }
  #apply_himg img{
    width: 130px;
    height: 130px;
    margin-left: 100px;
    /*border: 1px solid green;*/
  }
  #apply_name_box{
    padding-top: 20px;
    width: 100%;
    color: white;
    /*border: 1px solid #979468;*/
  }
  #apply_name_box span{
    display: block;
    width: 100%;
    text-align: center;
    margin: 5px;
  }
  #u_wx_name{
    font-weight: 8;
    font-size: 18px;
  }
  #u_wx_no{
    font-size: 12px;
  }
  #select_bar{
    background: #F2F2F2;
    position: fixed;
    bottom: 0;
    left: 0;
    padding: 20px;
    width: 100%;
    height: 30%;
    -webkit-border-radius:1%;
    -moz-border-radius:1%;
    -ms-border-radius:1%;
    -o-border-radius:1%;
  }
  #select_bar > div{
    text-align: left;
    width: 100%;
    height: 30px;
  }
  #select_bar span{
    width: 35px;
    height: 20px;
    padding: 5px 17px;
    margin: 5px 3px;
    background: white;
  }
  #detail_addr{
    width: 100%;
  }

  #number_lable{
    float: left;
    color: #666;
    font-size: 16px;
    height: 40px;
    line-height: 40px;
  }
  .number_right{
    float: right;
  }
  ._width{
    width: 98%;
  }
  #number_div{
    background: white;
    margin-top: 10px;
    height: 40px;
  }
  #number_inc{
    float: right;
    width: 20px;
    height: 20px;
    margin-top: 10px;
    right: 20px;
  }
  #number_value{
    font-size: 20px;
    right: 50px;
    color: #555;
  }
  #number_dec{
    float: right;
    width: 20px;
    height: 20px;
    margin-top: 10px;
    right: 80px;
  }

  #addr_pop_search{
    width: 100%;
    height: 100%;
    background: white;
    bottom: 0px;
  }
  #addr_icon{
    margin-top: 0px;
    width: 18px;
    height: 23px;
  }
  #addr_receive{
    font-size: 12;

  }
</style>


