<template>
  <div class="page" @click="$emit('select')">
    <div id="search_box">
      <img src="../../static/images/搜索@2x.png">
      <input placeholder="请输入所在小区或附近建筑物" v-model="addrInput" v-on:input="onInput" @click="inputCursor">
    </div>
    <div class="cell addr_box" v-for="addr in addrRes" @click="addrItemClick(addr)">
      <div class="addr_detail">{{addr.detail}}</div>
      <div class="addr_city">{{addr.city}}</div>
      <img class="addr_arrow" src="../../static/images/arrow-right@2x.png">
    </div>
  </div>
</template>

<script>

export default {
  name: 'me',
  data () {
    return {
      addrInput:'',
      addrRes:[],
    }
  },
  created(){
    
  },
  methods:{
    addrItemClick(addr){
      this.$emit("select",addr);
    },
    onInput(){
      let req = {
        key:'c654249bf032c01f4924ee95ac160252',
        keywords:this.addrInput,
        batch:true,
      }
      let self = this;
      this.post('http://restapi.amap.com/v3/assistant/inputtips?parameters',req,function(res){
        // console.log(res);
        let list = res.data.tips;
        self.addrRes = [];
        for(let i=0; i < list.length;i++){
          let netAddr = list[i];        
          let loc = netAddr.location+"";
          let locAry = loc.split(",");
          let addrWhole = netAddr.district + netAddr.name;
          let locAddr = {
            lat:locAry[0],
            lng:locAry[1],
            adcode:netAddr.adcode,
            detail:netAddr.name,
            addr:addrWhole,
            city:netAddr.district,
            district:netAddr.district
          }
          self.addrRes.push(locAddr);
        }
      })
    },
    inputCursor(e){
      e.cancelBubble = true;
    },
  },
}
</script>

<style scoped>
#search_box{
  width: 100%;
  height: 40px;
  background: white;
  border-bottom: 1px solid #E0E0E0;
}
#search_box img{
  width: 20px;
  height: 20px;
  padding: 10px;
  line-height: 40px;
  float: left;
}
#search_box input{
  width: 80%;
  height: 35px;
  line-height: 40px;
  float: left;
  text-align: left;
  border: none;
  outline:medium;
}
.addr_box{
  width: 100%;
  height: 45px;
  padding: 10px;
  background: white;
  border-bottom: 
}
.addr_detail{
  width: 85%;
  overflow-x: hidden;
  height: 30px;
  line-height: 30px;
  float: left;
  text-align: left;
  font-size: 16px;
  padding-right: 60px;
}
.addr_city{
  width: 85%;
  overflow-x: hidden;
  height: 20px;
  line-height: 20px;
  float: left;
  text-align: left;
  font-size: 14px;
  padding-right: 60px;
}
.addr_arrow{
  position: fixed;
  margin-top: 15px;
  right: 21px;
  width: 11px;
  height: 19px;
}
</style>


